import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, QueryList, ViewChildren } from '@angular/core';
import { SBSortableHeaderDirective, SortEvent } from '@app/modules/push/directives';
import { PushMessageModel } from '@myclapp/myclapp-server';
import { Observable } from 'rxjs';
import { PushService } from '../../../../../services';

@Component({
    selector: 'sb-ng-bootstrap-table-push',
    changeDetection: ChangeDetectionStrategy.OnPush,
    templateUrl: './ng-bootstrap-table.component.html',
    styleUrls: ['ng-bootstrap-table.component.scss'],
})
export class NgBootstrapTablePushComponent implements OnInit {
    @Input() pageSize = 4;

    pushMessages$!: Observable<PushMessageModel[]>;
    total$!: Observable<number>;
    sortedColumn!: string;
    sortedDirection!: string;

    @ViewChildren(SBSortableHeaderDirective) headers!: QueryList<SBSortableHeaderDirective>;

    constructor(
        public pushService: PushService,
        private changeDetectorRef: ChangeDetectorRef,
    ) {
    }

    ngOnInit() {
        this.pushService.pageSize = this.pageSize;
        this.pushMessages$ = this.pushService.results$;
        this.total$ = this.pushService.total$;
    }

    onSort({ column, direction }: SortEvent) {
        this.sortedColumn = column;
        this.sortedDirection = direction;
        this.pushService.sortColumn = column;
        this.pushService.sortDirection = direction;
        this.changeDetectorRef.detectChanges();
    }
}
